import * as React from "react";
import * as ReactDOM from "react-dom";
import {Provider} from "react-redux";
import {createStore} from "redux";

import {EditingPane} from "components/EditingPane";
import {IAppState, IAction, IPaletteAction} from "app";

const initialState = {
  selectedShape: undefined,
};

function reducer(state: IAppState = initialState, action: IAction): IAppState {
  switch (action.type) {
    case "SELECT_SHAPE":
      return {
        selectedShape: (action as IPaletteAction).shapeType,
      };
  }
  return state;
}

const store = createStore(reducer);

ReactDOM.render(
  <Provider store={store}>
    <EditingPane />
  </Provider>,
  document.getElementById("editor")
);
