import * as React from "react";
import * as Styles from "components/ShapesPalette.css";
import {connect} from "react-redux";
import {IAppState, IAction, IPaletteAction, ShapeType} from "app";
import {Circle, Square, Triangle} from "components/Shapes";

interface IProps {
  selectedShape?: ShapeType;
}

interface IPaletteItemProps {
  shapeType: ShapeType;
  onClick(shapeType: ShapeType): void;
}

interface IDispatch {
  dispatch(action: IAction): void;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShape: state.selectedShape,
  };
}

class PaletteItem extends React.Component<IPaletteItemProps> {
  public render(): JSX.Element {
    return (
      <div onClick={this.clickHandler}>
        <svg width={100} height={100}>
          {this.props.children}
        </svg>
      </div>
    );
  }

  private clickHandler = () => {
    this.props.onClick(this.props.shapeType);
  };
}

class ShapesPaletteBase extends React.Component<IProps & IDispatch> {
  public render(): JSX.Element {
    const {selectedShape} = this.props;
    return (
      <div className={Styles.palette}>
        <PaletteItem shapeType={ShapeType.Circle} onClick={this.clickHandler}>
          <Circle x={20} y={20} isSelected={selectedShape === ShapeType.Circle} />
        </PaletteItem>
        <PaletteItem shapeType={ShapeType.Square} onClick={this.clickHandler}>
          <Square x={20} y={20} isSelected={selectedShape === ShapeType.Square} />
        </PaletteItem>
        <PaletteItem shapeType={ShapeType.Triangle} onClick={this.clickHandler}>
          <Triangle x={20} y={20} isSelected={selectedShape === ShapeType.Triangle} />
        </PaletteItem>
      </div>
    );
  }

  private clickHandler = (shapeType: ShapeType) => {
    this.props.dispatch({type: "SELECT_SHAPE", shapeType} as IPaletteAction);
  };
}

export const ShapesPalette = connect(mapStateToProps)(ShapesPaletteBase);
