import * as React from "react";
import * as Styles from "components/Shapes.css";

interface IProps {
  x: number;
  y: number;
  isSelected?: boolean;
}

const ShapeSize = 60;
const CommonSvgProps = {
  stroke: "black",
  strokeWidth: "1",
  fill: "#dfe3fb",
};

export class Circle extends React.Component<IProps> {
  public render(): JSX.Element {
    const {x, y} = this.props;
    return (
      <circle
        cx={x + ShapeSize / 2}
        cy={y + ShapeSize / 2}
        r={ShapeSize / 2}
        className={this.props.isSelected ? Styles.selected : undefined}
        {...CommonSvgProps}
      />
    );
  }
}

export class Square extends React.Component<IProps> {
  public render(): JSX.Element {
    const {x, y} = this.props;
    return (
      <rect
        x={x}
        y={y}
        width={ShapeSize}
        height={ShapeSize}
        className={this.props.isSelected ? Styles.selected : undefined}
        {...CommonSvgProps}
      />
    );
  }
}

export class Triangle extends React.Component<IProps> {
  public render(): JSX.Element {
    const h = ShapeSize * 0.86;
    const {x, y} = this.props;
    const points: string[] = [[x + ShapeSize / 2, y].join(","), [x, y + h].join(","), [x + ShapeSize, y + h].join(",")];
    return (
      <polygon
        points={points.join(" ")}
        className={this.props.isSelected ? Styles.selected : undefined}
        {...CommonSvgProps}
      />
    );
  }
}
